// import logo from './logo.svg';
// import './index.css';
import './index.css'
import NavBar from './components/NavBar';
import Footer from './components/Footer';
import {BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Home from './components/Home/Home';
import Mercado from './components/Mercado/Mercado';
import Cotizador from './components/Cotizador/Cotizador';
import Servicios from './components/Servicios/Servicios';
import MundoEmprendedor from './components/MundoEmprendedor/MundoEmprendedor';
import Login from './components/Login/Login';
import SignIn from './components/Signin/Signin';
import Planes from './components/Planes/Planes';
import Quienes from './components/QuienesSomos/Quienes';
import ComoFunciona from './components/ComoFunciona/ComoFunciona';
import Faq from './components/Faq/Faq';



function App() {

  return (
    <>
      <Router>
        <NavBar />
        <Switch>   
          <Route path="/preguntas-frecuentes" exact ><Faq /></Route>
          <Route path="/como-funciona" exact ><ComoFunciona /></Route>
          <Route path="/quienes-somos" exact ><Quienes/></Route>
          <Route path="/plans" exact ><Planes/></Route>
          <Route path="/signin" exact ><SignIn/></Route>
          <Route path="/login" exact ><Login/></Route>  
          <Route path="/mundo-emprendedor" exact ><MundoEmprendedor/></Route>
          <Route path="/servicios" exact ><Servicios/></Route>
          <Route path="/cotizador" exact ><Cotizador/></Route>
          <Route path="/mercado-de-oportunidades" exact ><Mercado/></Route>
          <Route path="/" exact ><Home/></Route>
        </Switch>
        <Footer />
      </Router>
    </>
  );
}

export default App;
