import React from 'react';
import { Link } from 'react-router-dom';
import SelectorProveedor from './SelectorProveedor';

const Signin = () => {


  return (
    <>
      <div className="mt-28 md:mt-48">
        <div className="container mx-auto p-8">
          <h2 className="text-center my-4 titles text-primary text-3xl uppercase">Inicio de sesin o Registro</h2>
          <div className="flex justify-center ">
            <div className="bg-primary flex px-1 py-1 rounded-full shadow-xl shadow-gray-300 hover:shadow-gray-400">
              <Link to="/login">
                <div className="px-8 py-3 rounded-full">
                  <p className="text-body text-white font-bold">Iniciar Sesin</p>
                </div>
              </Link>
              <Link to="/signin">
                <div className="px-8 py-3 bg-white rounded-full">
                  <p className="text-body text-primary font-bold">Registrarme</p>
                </div>
              </Link>
            </div>
          </div>

          <SelectorProveedor />

        </div>
      </div>  
    </>
  )
}

export default Signin