import React from "react";
import QuienesImg from "../../assets/images/quienes/quienes-somos.png";
import Valor1 from "../../assets/images/quienes/valor1.png";
import Valor2 from "../../assets/images/quienes/valor2.png";
import Valor3 from "../../assets/images/quienes/valor3.png";
import Valor4 from "../../assets/images/quienes/valor4.png";

const Valores = [
  {img: Valor1, texto: "Aprendemos da a da"},
  {img: Valor2, texto: "Empoderamos a otros"},
  {img: Valor3, texto: "Somos transparentes"},
  {img: Valor4, texto: "Trabajamos en equipo"},
]

const Quienes = () => {
  return (
    <>
      <div className="mt-28 md:mt-44">
        <div className="container mx-auto p-8">
          <img className="mx-auto" src={QuienesImg} alt="" />
          <div className="w-10/12 mx-auto ">
            <h2 className="my-12 titles text-primary text-3xl uppercase">
              Concenos
            </h2>
            <p className="text-body">
              Te Proveo, parte de xxxxxx soluciones y negocios digitales, es una
              empresa creada el 2022, con el fin de acercar proveedores y
              emprendedores de una forma amigable y dinmica, te Proveo permite
              al usuario solicitar en forma gratuita cotizaciones de algn
              producto o servicio y de esta forma recibir propuestas de hasta 4
              de diferentes proveedores.
            </p>
            <p className="text-body mt-6">
              Queremos consolidarnos como la ms grande plataforma de comercio
              entre proveedores y consumidores
            </p>
          </div>

          <div className="mt-12 flex w-10/12 mx-auto bg-light-light p-2 flex-wrap">
            <div className="w-full lg:w-3/12 my-4 titles text-primary text-3xl uppercase border-l-4 px-6 border-primary">
              <h2 className="leading-10">
                Nuestra Misin
              </h2>
            </div>
            <div className="w-full lg:w-9/12 flex items-center h-auto">
              <p className="text-body ">Abrir oportunidades de negocio a todos quienes quieran emprender o hacer crecer su negocio.</p>
            </div>
          </div>
          
          <div className="mt-12 flex w-10/12 mx-auto bg-light-light p-2 flex-wrap">
            <div className="w-full lg:w-3/12 my-4 titles text-primary text-3xl uppercase border-l-4 px-6 border-primary">
              <h2 className="leading-10">
              Nuestra visin
              </h2>
            </div>
            <div className="w-full lg:w-9/12 flex items-center h-auto">
              <p className="text-body ">Ser la mayor comunidad de intercambio de bienes y servicios para todos quienes quieran emprender.</p>
            </div>
          </div>

          <div className="mt-12 flex w-10/12 mx-auto bg-light-light p-2 flex-wrap">
            <div className="w-full lg:w-3/12 my-4 titles text-primary text-3xl uppercase border-l-4 px-6 border-primary">
              <h2 className="leading-10">
              Nuestros valores
              </h2>
            </div>
            <div className="w-full lg:w-9/12 flex flex-wrap items-center h-auto">
            
              {Valores.map((item, i) => (
              <div className="w-6/12 md:w-3/12 flex justify-center flex-col items-center flex-wrap p-2">
                <img src={item.img} alt="" />
                <p className="text-body text-primary my-2 text-center">{item.texto}</p>
              </div>
              ))}

            </div>
          </div>

        </div>
      </div>
    </>
  );
};

export default Quienes;
