import React from 'react'
import Enchufe from "../../assets/images/servicios/enchufe.svg";
import Pintura from "../../assets/images/servicios/pintura.svg";
import Jardineria from "../../assets/images/servicios/jardineria.svg";
import Mascotas from "../../assets/images/servicios/mascotas.svg";
import Albanil from "../../assets/images/servicios/albanil.svg";
import Cerrajero from "../../assets/images/servicios/cerrajero.svg";
import Seguridad from "../../assets/images/servicios/seguridad.svg";
import Mecanico from "../../assets/images/servicios/mecanico.svg";
import Tapiceria from "../../assets/images/servicios/tapiceria.svg";
import Carpinteria from "../../assets/images/servicios/carpinteria.svg";
import Instalaciones from "../../assets/images/servicios/instalaciones.svg";
import Mudanzas from "../../assets/images/servicios/mudanzas.svg";

const servicios = [
  {nombre: "Electricidad", img: Enchufe},
  {nombre: "Albail", img: Albanil},
  {nombre: "Cerrajero", img: Cerrajero},
  {nombre: "Pintura", img: Pintura},
  {nombre: "Jardinera", img: Jardineria},
  {nombre: "Mascotas", img: Mascotas},
  {nombre: "Seguridad", img: Seguridad},
  {nombre: "Mecnico", img: Mecanico},
  {nombre: "Tapicera", img: Tapiceria},
  {nombre: "Carpintera", img: Carpinteria},
  {nombre: "Instalaciones", img: Instalaciones},
  {nombre: "Mudanzas", img: Mudanzas},
]

const CategoriaServicios = () => {
  return (
    <>
      <div className="container mx-auto">
        <h2 className="text-center my-8 titles text-primary text-3xl uppercase">Encuentra los mejores profesionales que tu hogar necesita</h2>
        <div className="flex flex-wrap my-8 w-10/12 mx-auto">

        {servicios.map((item, i) => (
          <div key={i} className="w-6/12 md:w-4/12 lg:w-2/12 p-2">
            <div className="card w-full flex justify-center rounded-xl shadow-xl shadow-gray-300 hover:shadow-gray-400 h-full">
              <div className="p-8">
                <img className="mx-auto my-2" src={item.img} alt="" />
                <p className="text-body font-bold text-primary my-4 text-center">{item.nombre}</p>
              </div>
            </div>
          </div>
          ))}
        </div>

        <div className="flex justify-center my-6">
          <button className="my-2 md:my-1 shadow-md hover:shadow-2xl transition-all duration-300 text-md btn-primary h-fit py-2 text-body px-6 lg:px-8 rounded-full text-white">
            Ver ms categoras
          </button>
        </div>


      </div>
    </>
  )
}

export default CategoriaServicios