import React, {useState,} from 'react'
import Perfil from "../../assets/images/perfil1.png";
import Icon from "../../assets/images/icon-loc.svg";
import FlameIcon from "../../assets/images/flame-icon.svg";
import ArrowIcon from "../../assets/images/arrow.svg";



const menuCotizaciones = ["Lorem Ipsum","Lorem Ipsum","Lorem Ipsum","Lorem Ipsum" ]
const cotizaciones = [
  {nombre: "Jane Doe",id: 1, ubicacion: "Santiago, Regin Metropolitana", descripcion: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Molestie aliquam diam vivamus morbi velit. Sagittis pulvinar dapibus...", fechaDia: "20", fechaMes: "Noviembre", img: Perfil},
  {nombre: "Jane Doe",id: 2, ubicacion: "Santiago, Regin Metropolitana", descripcion: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Molestie aliquam diam vivamus morbi velit. Sagittis pulvinar dapibus...", fechaDia: "20", fechaMes: "Noviembre", img: Perfil},
  {nombre: "Jane Doe",id: 3, ubicacion: "Santiago, Regin Metropolitana", descripcion: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Molestie aliquam diam vivamus morbi velit. Sagittis pulvinar dapibus...", fechaDia: "20", fechaMes: "Noviembre", img: Perfil}, 
  {nombre: "Jane Doe",id: 4, ubicacion: "Santiago, Regin Metropolitana", descripcion: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Molestie aliquam diam vivamus morbi velit. Sagittis pulvinar dapibus...", fechaDia: "20", fechaMes: "Noviembre", img: Perfil}
]

const Cotizacion = () => {

  const [isActive, setActive] = useState(false);
  const toggleClass = () => {
    setActive(!isActive);
  };

  const activeClass = "opacity-100 scale-100 translate-y-0";
  const inactiveClass = "opacity-0 lg:opacity-100 scale-0 lg:scale-100 absolute lg:relative -translate-y-full lg:translate-y-0";

  return (
    <>
    <section className="container px-4 lg:px-12 my-4 mx-auto">
      <div className="flex flex-wrap">
        <div className="w-full lg:w-1/5 px-3">
          <div className="shadow-xl rounded-lg bg-white shadow-gray-300">
            <div className="categories-cot lg:m-0 mt-4 mb-12">
              
                <div className="categories-cot-head p-4 btn-secondary rounded-lg md:rounded-t-lg md:rounded-b-none flex justify-between">
                  <p className="uppercase text-white font-bold lg:block hidden">Categoras ms cotizadas</p>
                  <button className="w-full lg:hidden flex justify-between" onClick={toggleClass}>
                    <p className="uppercase text-white font-bold ">Categoras ms cotizadas</p>
                    <img className="mt-2 w-5" src={ArrowIcon} alt="arrow" />
                  </button>
                </div>
              <div className={isActive ? activeClass : inactiveClass}>
                <div className="categories-cot-content rounded-b-full ">
                  <ul>
                  {menuCotizaciones.map((item, i) => (
                    <a key={i} href="www.google.com">
                      <li  className="hover:bg-slate-200 border-b border-b-slate-300 py-4 mx-4">{ item } </li>
                    </a>
                    ))}
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="w-full lg:w-4/5 px-6">
          <h2 className="mt-2 titles text-primary text-3xl uppercase">LTIMAS PETICIONES DE COTIZACIN</h2>
          <hr className="mt-4 border-slate-400" />
          <div className="flex flex-wrap">
            {cotizaciones.map((item, i) => (
              <div key={item.id} className="px-3 w-full md:w-6/12 ">
                <div className="card-op my-8 shadow-xl rounded-lg bg-white shadow-gray-300 py-4">
                  <div className="card-op-header flex">
                    <div className="w-4/12 md:w-3/12">
                      <img className="mx-auto md:mt-2 mt-10" src={item.img} alt="" />
                    </div>
                    <div className="w-8/12 md:w-9/12 relative">
                      <p className="absolute top-2 right-0 text-body bg-primary-light px-4 py-1 rounded-tl-lg rounded-bl-lg"><span className="font-bold">{item.fechaDia}</span> {item.fechaMes}</p>
                      <p className="mx-4 md:mx-1 mt-12 md:mt-6 font-bold text-body">{item.nombre}</p>
                      <div className="flex my-1">
                        <img className="mr-2" src={Icon} alt="" /> <p className="text-body">{item.ubicacion}</p>
                      </div>
                    </div>
                  </div>
                  <div className="card-op-content mt-6 px-6">
                    <p className="text-body">
                      {item.descripcion}
                      <a className="text-primary font-bold underline underline-offset-8" href="www.google.com"> Leer ms</a>
                    </p>
                    <div className="flex mt-6">
                      <img src={FlameIcon} alt="" />
                      <p className="mx-4 text-red">Quedan 2 cotizaciones!</p>
                    </div>
                  </div>
                  <div className="flex mt-4 justify-center">
                    <a className="btn-primary bg-secondary px-8 py-3 text-white text-body rounded-full shadow-xl shadow-gray-300 hover:shadow-gray-400" href="www.google.com">Proponer cotizacin</a>
                  </div>
                </div>
              </div>
            ))} 
          </div>
        </div>
      </div>
    </section>
    </>
  )
}

export default Cotizacion