import React from 'react'
import Perfil1 from "../../assets/images/profesionales/perfil1.png";
import Perfil2 from "../../assets/images/profesionales/perfil2.png";
import Perfil3 from "../../assets/images/profesionales/perfil3.png";
import Perfil4 from "../../assets/images/profesionales/perfil4.png";
import Perfil5 from "../../assets/images/profesionales/perfil5.png";
import Perfil6 from "../../assets/images/profesionales/perfil6.png";
import Star from "../../assets/images/profesionales/star.svg";

const Profesionales = [
  {profesion: "Diseo de interiores", nombre: "Margarita Barrientos", numProyectos: "125 Proyectos realizados", img: Perfil1},
  {profesion: "Mudanzas", nombre: "Alonso Prez", numProyectos: "112 Proyectos realizados", img: Perfil2},
  {profesion: "Fotografa", nombre: "Ricardo Avendao", numProyectos: "102 Proyectos realizados", img: Perfil3},
  {profesion: "Electricista", nombre: "Eduardo Gmez", numProyectos: "101 Proyectos realizados", img: Perfil4},
  {profesion: "Mecnico", nombre: "Jorge Inostrozas", numProyectos: "105 Proyectos realizados", img: Perfil5},
  {profesion: "Seguridad", nombre: "Miguel Bruna", numProyectos: "100 Proyectos realizados", img: Perfil6},
]


const NuestrosProfesionales = () => {
  return (
    <>
      <div className="container mx-auto">
        <div className="mx-auto w-11/12">
        <h2 className="text-center my-8 titles text-primary text-3xl uppercase">Conoce Nuestros profesionales mejor evaluados</h2>
        <div className="my-8 flex flex-wrap">

        {Profesionales.map((item, i) => (
          <div key={i} className="w-full sm:w-6/12 lg:w-1/3 p-2">
            <div className="card rounded-xl shadow-xl shadow-gray-300 hover:shadow-gray-400 h-full flex flex-wrap p-6">
              <div className="w-full flex lg:block justify-center lg:w-2/5 lg:p-2 xl:p-3">
                <img className="h-fit" src={item.img} alt="" />
              </div>
              <div className="w-full lg:w-3/5">
                <h4 className="text-body font-bold my-1 text">{item.profesion}</h4>
                <p className="text-body text-sm my-1">{item.nombre}</p>
                <div className="flex my-1">
                  <img src={Star} alt="" />
                  <img src={Star} alt="" />
                  <img src={Star} alt="" />
                  <img src={Star} alt="" />
                  <img src={Star} alt="" />
                </div>
                <p className="text-body text-sm my-2 font-bold">{item.numProyectos}</p>
                <a className="text-body text-primary font-bold underline underline-offset-8" href="https://www.google.cl/">
                  Visitar perfil
                </a>
              </div>
            </div>
          </div>
          ))}

        </div>
        </div>
      </div>
    </>
  )
}

export default NuestrosProfesionales