import React from 'react'
import Cotizacion from './Cotizacion'
import FiltroCotizador from './FiltroCotizador'

const Cotizador = () => {
  return (
    <div className="mt-32 md:mt-48">
      <div className="container mx-auto">
        <h2 className="text-center my-4 titles text-primary text-3xl uppercase">Encuentra el producto que necesitas</h2>
        <div className="flex flex-wrap py-4">
          <FiltroCotizador />
          <Cotizacion />
        </div>
      </div>
    </div>
  )
}

export default Cotizador