import React, {useState} from 'react'
import Idea from "../../assets/images/planes/idea.png";
import Cohete from "../../assets/images/planes/cohete.png";
import Cajas from "../../assets/images/planes/cajas.png";
import Check from "../../assets/images/planes/check.png";
import CheckGray from "../../assets/images/planes/checkgray.png";


const plans = [
  {icono: Idea, nombre: "Emprendedor", valor: 5000, descripcion: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Ullam, sed. ", caracteristicas: [{icono: Check, texto: "Lorem ipsum dolor sit amet."},{icono: Check, texto: "Lorem ipsum dolor sit amet."},{icono: CheckGray, texto: "Lorem ipsum dolor sit amet."},{icono: CheckGray, texto: "Lorem ipsum dolor sit amet."},{icono: CheckGray, texto: "Lorem ipsum dolor sit amet."},]
  },
  {icono: Cohete, nombre: "Comerciante", valor: 7000, descripcion: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Ullam, sed. ", caracteristicas: [{icono: Check, texto: "Lorem ipsum dolor sit amet."},{icono: Check, texto: "Lorem ipsum dolor sit amet."},{icono: Check, texto: "Lorem ipsum dolor sit amet."},{icono: CheckGray, texto: "Lorem ipsum dolor sit amet."},{icono: CheckGray, texto: "Lorem ipsum dolor sit amet."},]
  },
  {icono: Cajas, nombre: "Proveedor", valor: 10000, descripcion: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Ullam, sed. ", caracteristicas: [{icono: Check, texto: "Lorem ipsum dolor sit amet."},{icono: Check, texto: "Lorem ipsum dolor sit amet."},{icono: Check, texto: "Lorem ipsum dolor sit amet."},{icono: Check, texto: "Lorem ipsum dolor sit amet."},{icono: Check, texto: "Lorem ipsum dolor sit amet."},]
  },
]

const Planes = () => {

  const [OtrosPlanes, setOtrosPlanes] = useState(false);
  const muestraPlanes = () => {
    setOtrosPlanes(true);
  };

  return (
    <>
      <div className="mt-28 md:mt-60">
        <div className="container mx-auto">
          <h2 className="text-center my-4 titles text-primary text-3xl uppercase">Sacale partido a tu negocio</h2>
          <div className="w-full xl:w-10/12  mx-auto">
            <div className="flex flex-wrap py-4 justify-center">

              {plans.map((item, i)=> (
              <div key={i} className="w-full sm:w-6/12 lg:w-4/12 py-3 px-8 md:p-3">

                <div className="card w-full border rounded-lg shadow-lg shadow-slate-200">
                  <div className="card-head flex px-8 py-6">
                    <div className="bg-secondary border rounded-lg py-3 px-4 h-fit">
                      <img src={item.icono} alt="" />
                    </div>
                    <div className="text-body text-primary px-6">
                      <p>{item.nombre}</p>
                      <p className="font-bold text-2xl">{item.valor}</p>
                      <p className="ml-4 uppercase">+iva</p>
                    </div>
                  </div>
                  <div className="card-content px-8 pb-6">
                    <p className="text-body">
                      {item.descripcion}               
                    </p>
                    <hr className="mt-4 border-slate-500" />
                    <div className="list">

                    {item.caracteristicas.map((carac, i)=> (
                      <div key={i} className="flex justify-center md:justify-start py-3">
                        <img className="h-fit" src={carac.icono} alt="" />
                        <p className="mx-2">{carac.texto} </p>
                      </div>
                    ))}

                    </div>
                    <div className="flex mt-4 justify-center">
                      <a className="btn-primary bg-secondary px-8 py-3 text-white text-body rounded-full shadow-xl shadow-gray-300 hover:shadow-gray-400" href="www.google.com">Elegir Plan</a>
                    </div>
                  </div>
                </div>

              </div>
              ))}

            </div>

            <div className="flex justify-center my-12">
              <button onClick={muestraPlanes} className="text-center text-body text-primary font-bold text-lg hover:underline decoration-sky-900 underline-offset-8">
                Necesitas contratar por menos meses con los mismo beneficios? Revisa aqu
              </button>
            </div>
            
            <div className={OtrosPlanes ? "block" : "hidden"}>
              <h2 className=" my-4 titles text-primary text-xl uppercase">Valor Semestral</h2>
              <div className="flex flex-wrap py-4 justify-center">

                {plans.map((item, i)=> (
                <div key={i} className="w-full sm:w-6/12 lg:w-4/12 py-3 px-8 md:p-3">

                  <div className="card w-full border rounded-3xl shadow-lg shadow-slate-200">
                    <div className="card-head flex px-8 py-6">
                      <div className="bg-secondary border rounded-lg py-3 px-4 h-fit">
                        <img src={item.icono} alt="" />
                      </div>
                      <div className="text-body text-primary px-6">
                        <p>{item.nombre}</p>
                        <p className="font-bold text-2xl">{item.valor}</p>
                        <p className="ml-4 uppercase">+iva</p>
                      </div>
                    </div>
                    <div className="card-content px-8 pb-6">
                      <p className="text-body">
                        {item.descripcion}               
                      </p>

                      <div className="flex mt-4 justify-center">
                        <a className="btn-primary bg-secondary px-8 py-3 text-white text-body rounded-full shadow-xl shadow-gray-300 hover:shadow-gray-400" href="www.google.com">Elegir Plan</a>
                      </div>
                    </div>
                  </div>

                </div>
                ))}

              </div>
              <h2 className=" my-4 titles text-primary text-xl uppercase">Valor Trimestral</h2>
              <div className="flex flex-wrap py-4 justify-center">

                {plans.map((item, i)=> (
                <div key={i} className="w-full sm:w-6/12 lg:w-4/12 py-3 px-8 md:p-3">

                  <div className="card w-full border rounded-3xl shadow-lg shadow-slate-200">
                    <div className="card-head flex px-8 py-6">
                      <div className="bg-secondary border rounded-lg py-3 px-4 h-fit">
                        <img src={item.icono} alt="" />
                      </div>
                      <div className="text-body text-primary px-6">
                        <p>{item.nombre}</p>
                        <p className="font-bold text-2xl">{item.valor}</p>
                        <p className="ml-4 uppercase">+iva</p>
                      </div>
                    </div>
                    <div className="card-content px-8 pb-6">
                      <p className="text-body">
                        {item.descripcion}               
                      </p>

                      <div className="flex mt-4 justify-center">
                        <a className="btn-primary bg-secondary px-8 py-3 text-white text-body rounded-full shadow-xl shadow-gray-300 hover:shadow-gray-400" href="www.google.com">Elegir Plan</a>
                      </div>
                    </div>
                  </div>

                </div>
                ))}

              </div>        
            </div>            

          </div>
        </div>
      </div>
    </>
  )
}

export default Planes