import React from 'react'
import GridEmprendedor from './GridEmprendedor'
import PageNumber from './PageNumber'

const MundoEmprendedor = () => {
  return (
    <>
      <div className="mt-24 md:mt-36">
        <div className="container mx-auto">
          <GridEmprendedor />
          <PageNumber />
        </div>
      </div>
    </>
  )
}

export default MundoEmprendedor