import React from 'react'
import Arrow from "../../assets/images/arrowpages.svg";


const PageNumber = () => {

  const Numbers = [1, 2, 3, 4, 5]

  return (
    <>
      <div className=" mx-auto w-fit flex justify-center align-center">
        <div className="pages page-arrow flex justify-center align-center p-3 border-t-2 border-b-2 border-l-2 rounded-tl-xl rounded-bl-xl">
          <img className="w-3" src={Arrow} alt="" />
        </div>
        {Numbers.map((item, i) => (
        <div key={i} className="pages border-t-2 border-b-2 border-r border-l px-4 py-3 ">
          <p className="text-body font-bold text-primary">{item}</p>
        </div>
        ))}
        <div className="pages page-arrow flex justify-center align-center p-3 rotate-180 rounded-tl-xl rounded-bl-xl border-t-2 border-b-2 border-l-2">
          <img className="w-3" src={Arrow} alt="" />
        </div>
      </div>
    </>
  )
}

export default PageNumber