import React, {useState, useEffect} from "react";
import "../index.css";
import "../assets/css/main.css";
import Logo from "../assets/images/logo.png";
import Bell from "../assets/images/bell-icon.svg";
import Login from "../assets/images/login-icon.svg";
import { NavLink, Link } from 'react-router-dom';


const NavBar = () => {

  const [isActive, setActive] = useState(false);
  const toggleClass = () => {
    setActive(!isActive);
  };
  const NavList = [
    // {nombre: "Home", path: "/"},
    {nombre: "Mercado de Oportunidades", path: "/mercado-de-oportunidades"},
    {nombre: "Cotizador", path: "/cotizador"}, 
    {nombre: "Servicios", path: "/servicios"}, 
    {nombre: "Mundo Emprendedor", path: "/mundo-emprendedor"}, 
  ]

  const navWithOutScroll = "nav-bar-content w-full xl:w-9/12 mx-auto"
  const navWithScroll = "nav-bar-content nav-bar-content-scroll w-full xl:w-9/12 mx-auto"


  const [scroll, setScroll] = useState(false);
  useEffect(() => {
    window.addEventListener("scroll", () => {
      setScroll(window.scrollY > 50);
    });
  }, []);

  return (
    <div className="container">
      <div className="nav-bar fixed z-20 top-0 w-full shadow-lg hover:shadow-2xl transition-all duration-300">
        <div className={scroll ? navWithScroll : navWithOutScroll} style={{ height: isActive ? "auto" : ""}}>
          <div className="container mx-auto">
            <div className="flex justify-between lg:items-center pb-2 px-16">

              <Link to="/">
                <img className={scroll ? "w-20 md:w-24" : "w-20 md:w-32"} src={Logo} alt="Logo Te Proveo" />
              </Link>

              <ul className="hidden lg:flex lg:flex-row items-center justify-center mt-8 md:mt-0">
                {NavList.map((item, i) => (
                <li key={i} className="mr-0  mb-0 md-mb-8 md:mb-0 md:mr-6 md:w-auto w-full ease-linear hover:bg-slate-200 md:hover:bg-white py-4">
                  <NavLink to={item.path} exact activeClassName="nav-active" className="text-body text-black nav-item text-center 2xl:text-left flex w-min md:w-auto mx-auto">
                    {item.nombre}
                  </NavLink>
                </li>
                ))}
              </ul>

              <li className="flex mt-10 md:mt-6 lg:hidden mr-0  mb-8 md:mb-0 md:mr-0 w-min md:w-auto">
                <button onClick={toggleClass}>
                  <div className={isActive ? "menu-burger burger-active mb-2" : "menu-burger mb-2"}></div>
                </button>
              </li>

              <div className="hidden lg:flex login-section items-center">
                <div className="flex h-auto">
                  <button>
                    <img className=" mx-4" src={Bell} alt="Notification icon" />
                  </button>
                  <button className="transition-all btn-primary h-fit text-body font-bold px-5 text-black rounded flex items-center">
                    <img className="px-2" src={Login} alt="" />
                    <NavLink to="/login">
                      <span className="px-2">Entrar</span>
                    </NavLink>
                  </button>
                </div>
              </div>

            </div>

            <div className="menu text-lg mt-4">
              <ul className="flex flex-col lg:flex-row items-center justify-center mt-8 md:mt-0">
                <div className="flex lg:hidden login-section">
                  <button>
                    <img className="mb-8 mx-4" src={Bell} alt="Notification icon" />
                  </button>
                  <button className="transition-all btn-primary h-fit text-body font-bold px-5 text-black rounded flex items-center">
                    <img className="px-2" src={Login} alt="" />
                    <span className="px-2">Entrar</span>
                  </button>
                </div>
                
                {NavList.map((item, i) => (
                <li key={i} className="block lg:hidden mr-0  mb-0 lg:mb-0 lg:mr-6 lg:w-auto w-full ease-linear hover:bg-slate-200 lg:hover:bg-white py-4">
                  <NavLink to={item.path} exact activeClassName="nav-active" className="text-body text-black nav-item text-center lg:text-left flex w-min lg:w-auto mx-auto">
                    {item.nombre}
                  </NavLink>
                </li>
                ))}
                
              </ul>
            </div>

          </div>
        </div>
      </div>
    </div>
  );
};

export default NavBar;
