import React from 'react'
import Carousel from './Carousel'
import { Link } from 'react-router-dom';


const MainSection = () => {
  return (
    <>
      <section className="main-section mt-36 md:mt-48 container mx-auto">
        <h2 className="text-center my-12 titles text-primary text-3xl uppercase">El proveedor que necesitas, est aqu</h2>
        <div className="flex flex-col md:flex-row mt-2 w-9/12 mx-auto justify-evenly items-center">
          <Link to="/signin">
            <button className="my-4 md:my-1 shadow-md hover:shadow-2xl transition-all duration-300 text-lg btn-primary h-fit py-3 text-body-bold px-10 lg:px-28 text-white rounded-full flex items-center uppercase justify-center">
              Soy comprador
            </button>
          </Link>
          <Link to="/signin">
            <button className="my-4 md:my-1 shadow-md hover:shadow-2xl transition-all duration-300 transition-all text-lg btn-primary h-fit py-3 text-body-bold px-10 lg:px-28 text-white rounded-full flex items-center uppercase justify-center">
              Soy proveedor
            </button>
          </Link>
        </div>
      </section>
      <section className="container my-4 md:my-16 mx-auto">
        <Carousel />
      </section>
    </>
  )
}

export default MainSection