import React from 'react'
import Torta from "../../assets/images/torta.png";
import Polera from "../../assets/images/polera.png";
import Switch from "../../assets/images/switch.png";
import Tazones from "../../assets/images/tazones.png";
import Cajas from "../../assets/images/cajas.png";
import Sofa from "../../assets/images/sofa.png";
import IconLoc from "../../assets/images/icon-loc.svg";
import PageNumber from './PageNumber';


const productos = [
  {imagen: Torta, nombreProducto: "TSagittis pulvinar dapibus egestas dignissim mollis in etiam eu nunc.", nombreLugar: "The Cake Maker Shop", ubicacion: "Santiago, Regin Metropolitana", precio: 100000,fechaDia: "20", fechaMes: "Noviembre",},
  {imagen: Polera, nombreProducto: "TSagittis pulvinar dapibus egestas dignissim mollis in etiam eu nunc.", nombreLugar: "Poleras Tomy", ubicacion: "Santiago, Regin Metropolitana", precio: 100000,fechaDia: "20", fechaMes: "Noviembre",},
  {imagen: Switch, nombreProducto: "TSagittis pulvinar dapibus egestas dignissim mollis in etiam eu nunc.", nombreLugar: "Reparaciones Len", ubicacion: "Santiago, Regin Metropolitana", precio: 100000,fechaDia: "20", fechaMes: "Noviembre",},
  {imagen: Tazones, nombreProducto: "TSagittis pulvinar dapibus egestas dignissim mollis in etiam eu nunc.", nombreLugar: "Impresiones Paris", ubicacion: "Santiago, Regin Metropolitana", precio: 100000,fechaDia: "20", fechaMes: "Noviembre",},
  {imagen: Cajas, nombreProducto: "TSagittis pulvinar dapibus egestas dignissim mollis in etiam eu nunc.", nombreLugar: "Cajas Carlos", ubicacion: "Santiago, Regin Metropolitana", precio: 100000,fechaDia: "20", fechaMes: "Noviembre",},
  {imagen: Sofa, nombreProducto: "TSagittis pulvinar dapibus egestas dignissim mollis in etiam eu nunc.", nombreLugar: "Tapicera Mara", ubicacion: "Santiago, Regin Metropolitana", precio: 100000,fechaDia: "20", fechaMes: "Noviembre",},
]


const MercadoProductos = () => {
  return (
    <>
      <div className="w-full lg:w-3/5 mx-auto">
        <div className="flex w-full justify-end px-4">
          <p className="font-bold text-body text-primary mr-4">Ordenar por: </p>
          <select  defaultValue="" className="px-3 rounded-md border focus-visible:outline-sky-400 border-solid border-sky-400" name="select">
            <option value="">Fecha de publicacin</option>
            <option value="date1">Fecha 1</option>
            <option value="date2">Fecha 2</option>
          </select>
        </div>
        <div className="flex justify-end flex-wrap p-4 lg:p-0">

          {productos.map((item, i)=> (
          <div key={i} className=" w-full lg:w-11/12 card-product-mercado border rounded-lg shadow-lg shadow-slate-200 relative my-4">
            <div className="flex p-8 flex-wrap">
              <p className="absolute top-6 right-0 text-body bg-primary-light px-4 py-1 rounded-tl-lg rounded-bl-lg"><span className="font-bold">{item.fechaDia}</span> {item.fechaMes}</p>
              <div className="w-full lg:w-2/6 flex justify-center lg:block ">
                <img className="object-contain lg:mx-auto" src={item.imagen} alt="" />
              </div>
              <div className="w-full lg:w-4/6 p-5">
                <h3 className="text-xl text-body font-bold text-primary mb-2">{item.nombreLugar}</h3>
                <div className="flex my-4 justify-between">
                  <p className="text-body font-bold">{item.nombreLugar}</p>
                  <div className="mx-2 flex">
                    <img className="mx-2" src={IconLoc} alt="" />
                    <p className="text-body">{item.ubicacion}</p>
                  </div>
                </div>
                <p className="text-xl text-body font-bold text-primary ">$ {item.precio}</p>
                <div className="flex mt-4 justify-center">
                  <a className="btn-primary bg-secondary px-8 py-3 text-white text-body rounded-full shadow-xl shadow-gray-300 hover:shadow-gray-400" href="www.google.com">Solicitar</a>
                </div>
              </div>
            </div>
          </div>
          ))}
        </div>
        <PageNumber />

      </div>
    </>
  )
}

export default MercadoProductos