import React from 'react'
import Cover from "../../assets/images/comofunciona/comofunciona.png";
import Circulo from "../../assets/images/comofunciona/circulo.png";
import Funcion1 from "../../assets/images/comofunciona/1.png";
import Funcion2 from "../../assets/images/comofunciona/2.png";
import Funcion3 from "../../assets/images/comofunciona/3.png";
import Funcion4 from "../../assets/images/comofunciona/4.png";
import Funcion5 from "../../assets/images/comofunciona/5.png";
import Funcion6 from "../../assets/images/comofunciona/6.png";

const Funciones = [
  {numero: 1, img: Funcion1, titulo: "Regstrate y confirma tu correo."},
  {numero: 2, img: Funcion2, titulo: "Inicia sesin."},
  {numero: 3, img: Funcion3, titulo: "Dirgite al cotizador."},
  {numero: 4, img: Funcion4, titulo: "Publica lo que necesitas, con detalles como color, forma, tamao, fecha, hora y lugar."},
  {numero: 5, img: Funcion5, titulo: "Espera la propuesta que ms se acomode a ti, recibirs un mximo de 4, selecciona la que ms te gusta."},
  {numero: 6, img: Funcion6, titulo: "El vendedor que elegiste se comunicar contigo y listo !."},
]

const Funciones2 = [
  {numero: 1, img: Funcion1, titulo: "Regstrate y confirma tu correo."},
  {numero: 2, img: Funcion2, titulo: "Inicia sesin."},
  {numero: 3, img: Funcion3, titulo: "Dirgite al cotizador."},
  {numero: 4, img: Funcion4, titulo: "Busca la propuesta que ms se acomode a tu negocio."},
  {numero: 5, img: Funcion5, titulo: "Escribe tu propuesta de cotizacin."},
  {numero: 6, img: Funcion6, titulo: "Comunicate con tu cliente para coordinar los detalles."},
]

const ComoFunciona = () => {
  return (
    <>
      <div className="mt-24 md:mt-40">
        <div className="container mx-auto">
          <div className="w-10/12 mx-auto">
            <img className="w-full " src={Cover} alt="" />
            <div className="comprar">
              <h2 className=" my-12 titles text-primary text-2xl uppercase">Si quieres comprar</h2>
              <div className="flex my-4 flex-wrap">

              {Funciones.map((item, i ) => (
                <div key={i} className="funcion flex flex-col w-full sm:w-6/12 md:w-4/12 lg:w-2/12 p-4">
                <div className="relative w-full flex flex-col justify-center items-center flex-wrap">
                  <img className="" src={Circulo} alt="" />
                  <span className="absolute top-3 inset-x-2/4 h-10 pr-3 ml-[-5px] text-white">{item.numero}</span>
                  <img className="w-fit px-4 my-6" src={item.img} alt="" />
                  <p className="text-body text-primary text-center">
                    {item.titulo}
                  </p>
                </div>
              </div>
              ))}
                

              </div>
            </div>
            <div className="vender">
              <h2 className=" my-12 titles text-primary text-2xl uppercase">Si quieres vender</h2>
              <div className="flex my-4 flex-wrap">

              {Funciones2.map((item, i ) => (
                <div key={i} className="funcion flex flex-col w-full sm:w-6/12 md:w-4/12 lg:w-2/12 p-4">
                <div className="relative w-full flex flex-col justify-center items-center flex-wrap">
                  <img className="" src={Circulo} alt="" />
                  <span className="absolute top-3 inset-x-2/4 h-10 pr-3 ml-[-5px] text-white">{item.numero}</span>
                  <img className="w-fit px-4 my-6" src={item.img} alt="" />
                  <p className="text-body text-primary text-center">
                    {item.titulo}
                  </p>
                </div>
              </div>
              ))}
                

              </div>
            </div>
            <div className="videos flex flex-wrap my-8">
              <div className="w-6/12 p-3">
                <iframe className="w-full aspect-video"  src="https://www.youtube.com/embed/pHCdS7O248g" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
              </div>
              <div className="w-6/12 p-3">
                <iframe className="w-full aspect-video"  src="https://www.youtube.com/embed/WGU_4-5RaxU" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default ComoFunciona