import React from 'react';
import { Link } from 'react-router-dom';


const Login = () => {
  return (
    <>
      <div className="searchbar mt-28 md:mt-48">
        <div className="container mx-auto p-8">
          <h2 className="text-center my-4 titles text-primary text-3xl uppercase">Inicio de sesin o Registro</h2>
          <div className="flex justify-center ">
            <div className="bg-primary flex px-1 py-1 rounded-full shadow-xl shadow-gray-300 hover:shadow-gray-400">
              <Link to="/login">
                <div className="px-8 py-3 bg-white rounded-full">
                  <p className="text-body text-primary font-bold">Iniciar Sesin</p>
                </div>
              </Link>
              <Link to="/signin">
                <div className="px-8 py-3 rounded-full">
                  <p className="text-body text-white font-bold">Registrarme</p>
                </div>
              </Link>
            </div>
          </div>
          <div className="card my-8 shadow rounded-lg bg-white shadow-gray-300 py-4 w-10/12 md:w-4/5 lg:w-3/5 mx-auto">
            <p className="text-body inline-block my-12 bg-primary-light px-4 pl-8 md:pl-16 py-1 rounded-tr-full rounded-br-full uppercase">Datos de usuario</p>
            <div className="w-4/5 md:w-3/5 xl:w-2/5  mx-auto">
              <label className="text-body my-2 block" htmlFor="email">Direccin de correo electrnico</label>
              <input required className="border border-slate-400 placeholder:text-slate-400 block bg-white w-full rounded py-3 pl-2 pr-3 shadow-sm focus:outline-none focus:border-sky-500 focus:ring-sky-500 focus:ring-1 " type="email" name="email"/>
              <label className="text-body mb-2 mt-6 block" htmlFor="pass">Contrasea</label>
              <input required className="border border-slate-400 placeholder:text-slate-400 block bg-white w-full rounded py-3 pl-2 pr-3 shadow-sm focus:outline-none focus:border-sky-500 focus:ring-sky-500 focus:ring-1 " type="email" name="pass"/>
              <a className="text-body font-bold underline underline-offset-4 my-6 inline-block" href="www.google.com">
              Has olvidado tu contrasea?
            </a>
            <button className="mt-4 mb-8 shadow-md hover:shadow-2xl transition-all duration-300 transition-all  btn-primary py-3 text-body-bold w-full text-white rounded-full flex items-center justify-center">
              Iniciar Sesin
            </button>
            </div>

          </div>
        </div>
      </div>  
    </>
  )
}

export default Login