import React, {useState} from "react";
import Proveedor from './Proveedor';
import Comprador from './Comprador';
import Servicios from './Servicios';


const SelectorProveedor = () => {
  const proveedor = "proveedor"
  const comprador = "comprador"
  const servicios = "servicios"


  const [selector, setSelector] = useState (proveedor)

  const cambioSelector = e =>{
    setSelector(e.target.value)
  }



  return (
    <>
      <div className="card my-8 shadow rounded-lg bg-white shadow-gray-300 py-4 w-10/12 lg:w-4/5 xl:w-3/5 mx-auto">
        <div className="flex justify-center">
          <form className="px-8 pt-6 flex align-center justify-center flex-wrap">
            <div className="inline-block w-full md:w-auto text-center my-2">
              <input
                type="radio"
                id="proveedores"
                name="proveedor"
                value={proveedor}
                checked={selector === proveedor ?  true : false}
                onChange={cambioSelector}
              />
              <label className="text-body px-4" htmlFor="proveedores">
                Soy proveedor
              </label>
            </div>

            <div className="inline-block w-full md:w-auto text-center my-2">
              <input
                type="radio"
                id="compradores"
                name="proveedor"
                value={comprador}
                checked={selector === comprador ?  true : false}
                onChange={cambioSelector}
              />
              <label className="text-body px-4" htmlFor="compradores">
                Soy comprador
              </label>
            </div>

            <div className="inline-block w-full md:w-auto text-center my-2">
              <input
                type="radio"
                id="servicios"
                name="proveedor"
                value={servicios}
                checked={selector === servicios ?  true : false}
                onChange={cambioSelector}
              />
              <label className="text-body px-4" htmlFor="servicios">
                Presto servicios
              </label>
            </div>
          </form>
        </div>

        <>
          {selector=== "proveedor" && (<Proveedor />)}
          {selector === "comprador" && (<Comprador />)}
          {selector === "servicios" && (<Servicios />)}
        </>

      </div>
    </>
  );
};

export default SelectorProveedor;
