import React, {useState,}  from 'react'
import Cover from "../../assets/images/faq/faq.png";

const preguntas = [
  {pregunta: "1Lorem ipsum dolor sit amet, consectetur adipiscing elit. Molestie duis amet viverra volutpat varius odio ac?", respuesta: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Inventore nisi rem quidem harum tenetur autem perspiciatis nemo laboriosam necessitatibus, velit debitis facere. Distinctio saepe iste fuga, architecto impedit natus odit?"},
  {pregunta: "2Lorem ipsum dolor sit amet, consectetur adipiscing elit. Molestie duis amet viverra volutpat varius odio ac?", respuesta: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Inventore nisi rem quidem harum tenetur autem perspiciatis nemo laboriosam necessitatibus, velit debitis facere. Distinctio saepe iste fuga, architecto impedit natus odit?"},
  {pregunta: "3Lorem ipsum dolor sit amet, consectetur adipiscing elit. Molestie duis amet viverra volutpat varius odio ac?", respuesta: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Inventore nisi rem quidem harum tenetur autem perspiciatis nemo laboriosam necessitatibus, velit debitis facere. Distinctio saepe iste fuga, architecto impedit natus odit?"},
  {pregunta: "4Lorem ipsum dolor sit amet, consectetur adipiscing elit. Molestie duis amet viverra volutpat varius odio ac?", respuesta: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Inventore nisi rem quidem harum tenetur autem perspiciatis nemo laboriosam necessitatibus, velit debitis facere. Distinctio saepe iste fuga, architecto impedit natus odit?"},
]


const Faq = () => {
  const [selected, setSelected] = useState(null);
  // const show = "text-body transition-all mx-2 h-auto max-h-full ease-[cubic-bezier(1,0,1,0)]"
  // const hidden = "text-body transition-all mx-2 max-h-0 overflow-hidden ease-[cubic-bezier(0,1,0,1)]"

  const toggle = (i) =>{
    if(selected === i){
      return setSelected(null)
    }

    setSelected(i)
  }

  return (
    <>
      <div className="mt-24 md:mt-40">
        <div className="container mx-auto">
          <div className="w-10/12 mx-auto">
            <img className="w-full " src={Cover} alt="" />

            <div className="container mx-auto">

              <div className="flex h-full w-full justify-center items-center">
                <div className="accordion">

                  {preguntas.map((item, i ) => (
                    <div key={i} className="item p-3 mb-2">
                      <div onClick={() => toggle(i)} className="title p-2 flex justify-between items-center cursor-pointer">
                        <h2 className="text-body font-bold my-3">{item.pregunta}</h2>
                        <span>{selected === i ? "" : ""}</span>
                      </div>
                      <div className="pb-2 border-b border-slate-600">
                        <div className={selected === i ? "content-accordion show" : "content-accordion"}>
                          <p className="text-body mx-2 mb-4">{item.respuesta}</p>
                        </div>
                      </div>
                    </div>
                  ))}

                </div>
              </div>

            </div>

            
          </div>
        </div>
      </div>
    </>
  )
}

export default Faq