import React from 'react'
import { Link } from 'react-router-dom';

const Unete = () => {
  return (
    <>
      <div className="container my-16 mx-auto">
        <h2 className="text-center my-12 titles text-primary text-3xl uppercase">NETE A LA COMUNIDAD MS GRANDE DE VENTAS Y SERVICIOS</h2>
        <p className="text-body text-center">
        Eres emprendedor, comerciante o Porveedor? <br />
Te tenemos cubierto, hay un plan para cada necesidad
        </p>
        <div className="flex justify-center mt-8">
          <Link to="/plans">
            <button className="my-4 md:my-1 shadow-md hover:shadow-2xl transition-all duration-300 transition-all text-lg btn-primary h-fit py-3 text-body-bold px-10 lg:px-28 text-white rounded-full flex items-center justify-center">
              Conocer planes
            </button>
          </Link>
        </div>
      </div>
    </>
  )
}

export default Unete