import React from 'react'
import WebPay from "../assets/images/webpay-logo.png";
import { Link } from 'react-router-dom';


const Footer = () => {
  return (
    <>
      <div className="bg-light-gray">
        <div className="container mx-auto px-6 py-10">
          <div className="flex flex-wrap">
            <div className="w-full md:w-2/5 p-4">
              <p className="text-body font-bold text-lg my-4">Contctanos</p>
              <div className="input-with-placeholder relative my-4">
                <input required className="placeholder:text-slate-400 block bg-white w-full md:w-3/4 rounded pt-3.5 pb-2 pl-2 pr-3 shadow-sm focus:outline-none focus:border-sky-500 focus:ring-sky-500 focus:ring-1 sm:text-sm input-contacto" type="text" name="nombre"/>
                <label className="text-gray absolute top-0 ml-2 label-contacto text-body" htmlFor="nombre">Nombre</label>
              </div>
              <div className="input-with-placeholder relative my-4">
                <input required className="placeholder:text-slate-400 block bg-white w-full md:w-3/4 rounded pt-3.5 pb-2 pl-2 pr-3 shadow-sm focus:outline-none focus:border-sky-500 focus:ring-sky-500 focus:ring-1 sm:text-sm input-contacto" type="email" name="nombre"/>
                <label className="text-gray absolute top-0 ml-2 label-contacto text-body" htmlFor="email">Email</label>
              </div>
              <div className="input-with-placeholder relative my-4">
                <textarea required="required" rows="5" cols="50" maxLength="250" className="placeholder:text-slate-400 block bg-white w-full md:w-3/4 rounded pt-3.5 pb-2 pl-2 pr-3 shadow-sm focus:outline-none focus:border-sky-500 focus:ring-sky-500 focus:ring-1 sm:text-sm input-contacto resize-none" type="text" name="mensaje"></textarea>
                <label className="text-gray absolute top-0 ml-2 label-contacto text-body" htmlFor="mensaje">Mensaje</label>
              </div>
              <div className="flex w-full md:w-3/4 mt-4 justify-end">
                <a className="btn-primary bg-secondary px-8 py-3 text-white text-body rounded-full shadow-xl shadow-gray-300 hover:shadow-gray-400" href="www.google.com">Enviar</a>
              </div>
            </div>
            <div className="w-full md:w-3/5 p-4 flex flex-wrap">
              <div className="w-6/12 lg:w-1/3">
                <p className="text-body font-bold text-lg my-4">Informacin</p>
                <a href="www.google.com"><p className="text-body text-lg my-4 hover:underline decoration-gray-400 underline-offset-8">Mi cuenta</p></a>
                <Link to="/plans">
                  <p className="text-body text-lg my-4 hover:underline decoration-gray-400 underline-offset-8">Planes</p>
                </Link>
                <Link to="/quienes-somos">
                  <p className="text-body text-lg my-4 hover:underline decoration-gray-400 underline-offset-8">Sobre Nosotros</p>
                </Link>
              </div>
              <div className="w-6/12 lg:w-1/3">
                <p className="text-body font-bold text-lg my-4">Soporte</p>
                <Link to="/como-funciona"><p className="text-body text-lg my-4 hover:underline decoration-gray-400 underline-offset-8">Cmo funciona</p></Link>
                <Link to="/preguntas-frecuentes"><p className="text-body text-lg my-4 hover:underline decoration-gray-400 underline-offset-8">Preguntas frecuentes</p></Link>
                <a href="www.google.com"><p className="text-body text-lg my-4 hover:underline decoration-gray-400 underline-offset-8">Trminos y condiciones</p></a>
              </div>
              <div className="w-full lg:w-1/3">
                <div className="flex justify-center mt-4">
                  <img src={WebPay} alt="" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Footer