import React from "react";
import Servicio1 from "../../assets/images/home/servicio1.png";
import Servicio2 from "../../assets/images/home/servicio2.png";
import Servicio3 from "../../assets/images/home/servicio3.png";
import Servicio4 from "../../assets/images/home/servicio4.png";

const servicios = [
  {id: 1, texto: "Solicita presupuesto gratis de un producto o servicio", img: Servicio1},
  {id: 2, texto: "Recibe hasta 4 cotizaciones", img: Servicio2},
  {id: 3, texto: "Elige la que ms te acomode", img: Servicio3},
  {id: 4, texto: "Califica a tu proveedor para ayudar a la comunidad", img: Servicio4},
]



const Servicios = () => {
  return <>
    <div className="container mx-auto bg-light-light">
      <div className="flex flex-wrap">
        {servicios.map((item)=> (
          <div key={item.id} className="flex flex-col p-10 items-center justify-center w-full sm:w-1/2 md:w-1/4 ">
            <img src={item.img} alt="" />
            <p className="p-4 text-lg text-center text-body font-bold text-primary">{item.texto} </p>
          </div>
        ))}
      </div>
    </div>
  </>;
};

export default Servicios;
