import React from 'react'
import SearchIcon from "../../assets/images/search-icon.svg";


const SearchBar = () => {
  return (
    <>
      <div className="searchbar mt-24 md:mt-36 bg-secondary">
        <div className="container mx-auto p-8">
          <div className="mx-auto relative w-4/5">
            <input required className="w-full placeholder:text-slate-400 block bg-white rounded-full py-4 pl-6 pr-3 shadow-sm focus:outline-none focus:border-sky-500 focus:ring-sky-500 focus:ring-1 sm:text-sm input-contacto mx-auto input-search-bar" type="text" name="buscar" placeholder="Buscar productos o vendedores"/>
            <button className="absolute top-4 right-8 px-2 linea-search">
              <img src={SearchIcon} alt="" />
            </button>  
          </div>
        </div>
      </div>

    </>
  )
}

export default SearchBar