import React, {useState,} from 'react'
import Filtro from "../../assets/images/filter.svg";


const FiltroCotizador = () => {

  const [isActive, setActive] = useState(false);
  const toggleClass = () => {
    setActive(!isActive);
  };

  const activeClass = "opacity-100 scale-100 translate-y-0 inset-x-2/4";
  const inactiveClass = "opacity-0 lg:opacity-100 scale-0 lg:scale-100 absolute lg:relative -translate-y-full lg:translate-y-0";

  return (
    <>
      <div className="w-full lg:w-1/5 p-4 lg:p-2">

        <button onClick={toggleClass} className="w-full block lg:hidden">
          <div className="flex justify-center">
            <img className="w-8" src={Filtro} alt="" /> <span className="px-4 text-body font-bold">Filtrar</span>
          </div>
          <hr className="my-3" />
        </button>

        <div className={isActive ? activeClass : inactiveClass}>
          <div className="w-3/5 lg:w-11/12 mx-auto lg:mx-0">

            <div className="palabraclave">
              <p className="font-bold text-body text-primary mr-4 my-2">Buscar por palabra clave</p>
              <input placeholder="Quesos, pasteles, cajas " className="px-3 rounded-md border focus-visible:outline-sky-400 border-solid border-sky-400 w-full" type="text" />
            </div>

            <div className="filtro-region my-8">
              <p className="font-bold text-body text-primary mr-4 my-2">Filtrar por regin</p>
              <select defaultValue="" className="px-3 rounded-md border focus-visible:outline-sky-400 border-solid border-sky-400 w-full" name="region">
                <option value="" >Elegir regin</option>
                <option value="value1">Regin 1</option>
                <option value="value2">Regin 2</option>
              </select>
            </div>

            <div className="filtro-region my-8">
              <p className="font-bold text-body text-primary mr-4 my-2">Filtrar por comuna</p>
              <select defaultValue="" className="px-3 rounded-md border focus-visible:outline-sky-400 border-solid border-sky-400 w-full" name="comuna">
                <option value="" >Regin comuna</option>
                <option value="value1">Comuna 1</option>
                <option value="value2">Comuna 2</option>
              </select>
            </div>

            <div className="filtro-region my-8">
              <p className="font-bold text-body text-primary mr-4 my-2">Filtrar por categora</p>
              <select defaultValue="" className="px-3 rounded-md border focus-visible:outline-sky-400 border-solid border-sky-400 w-full" name="categoria">
                <option value="" >Todas las categoras</option>
                <option value="value1">Categora 1</option>
                <option value="value2">Categora 2</option>
              </select>
            </div>

            <div className="filtro-cotizaciones my-8">
              <form>
                <input type="radio" id="cotizaciones" name="busqueda" value="1" />
                <label className="text-body px-4" htmlFor="cotizaciones">Buscar cotizaciones</label>
                <br />
                <input type="radio" id="proveedores" name="busqueda" value="2" />
                <label className="text-body px-4" htmlFor="proveedores">Buscar Proveedores</label>
              </form>

            </div>

            <div className="flex">
              <button className="my-4 md:my-1 shadow-md hover:shadow-2xl transition-all duration-300 h-fit py-2 text-body-bold text-secondary rounded-3xl flex items-center justify-center border border-amber-500 px-5 w-full hover:text-white hover:bg-amber-500">
                  Buscar
              </button>
            </div>
          </div>
        </div>

      </div>
    </>
  )
}

export default FiltroCotizador